
.bbs {
  border: 1px solid rgba(230, 215, 255, 0.12);
  border-radius: 10px;
  background: rgba(18, 6, 31, 0.98);
  padding: 10px;
}

.bbs__header {
  display: flex;
  align-items: baseline;
  gap: 10px;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(230, 215, 255, 0.10);
}

.bbs__brand {
  font-family: var(--mchan-font-mono);
  font-size: 12px;
  color: rgba(244, 238, 255, 0.86);
}

.bbs__logo {
  font-weight: 700;
  letter-spacing: 0.06em;
}

.bbs__tagline {
  color: rgba(244, 238, 255, 0.60);
}

.bbs__sep {
  opacity: 0.7;
  padding: 0 4px;
}

.bbs__status {
  font-family: var(--mchan-font-mono);
  font-size: 11px;
  color: rgba(182, 108, 255, 0.92);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.bbs__actions {
  margin-left: auto;
}

.bbs__link {
  border: 0;
  padding: 0;
  background: transparent;
  cursor: pointer;
  color: rgba(244, 238, 255, 0.70);
  font-family: var(--mchan-font-mono);
  font-size: 12px;
  text-decoration: underline;
}

.bbs__nav {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 10px 0;
}

.bbs__navItem {
  border: 1px solid rgba(230, 215, 255, 0.10);
  background: rgba(0, 0, 0, 0.14);
  color: rgba(244, 238, 255, 0.75);
  font-family: var(--mchan-font-mono);
  font-size: 12px;
  padding: 6px 8px;
  border-radius: 8px;
  cursor: pointer;
}

.bbs__navItem--active {
  border-color: rgba(182, 108, 255, 0.45);
  color: rgba(244, 238, 255, 0.92);
}

.bbs__navCount {
  opacity: 0.75;
  margin-left: 4px;
}

.bbs__tools {
  border: 1px solid rgba(230, 215, 255, 0.10);
  border-radius: 8px;
  padding: 8px;
  margin: 2px 0 10px;
  background: rgba(0, 0, 0, 0.16);
  font-size: 12px;
  color: rgba(244, 238, 255, 0.78);
}

.bbs__toolsBody {
  margin-top: 8px;
}

.bbs__main {
  font-size: 13px;
}

.bbs__boardTop,
.bbs__threadTop {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 6px 0 10px;
}

.bbs__boardTitle {
  font-family: var(--mchan-font-display);
  font-size: 16px;
}

.bbs__crumb {
  color: rgba(244, 238, 255, 0.70);
  font-family: var(--mchan-font-mono);
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.bbs__crumbBoard {
  color: rgba(244, 238, 255, 0.88);
}

.bbs__mono {
  font-family: var(--mchan-font-mono);
}

.bbs__listHead {
  display: grid;
  grid-template-columns: 64px 1fr 48px 120px;
  gap: 8px;
  padding: 6px 8px;
  border-top: 1px solid rgba(230, 215, 255, 0.10);
  border-bottom: 1px solid rgba(230, 215, 255, 0.10);
  background: rgba(0, 0, 0, 0.16);
  font-family: var(--mchan-font-mono);
  font-size: 12px;
  color: rgba(244, 238, 255, 0.70);
}

.bbs__rowItem {
  width: 100%;
  text-align: left;
  border: 0;
  background: transparent;
  color: inherit;
  cursor: pointer;
  display: grid;
  grid-template-columns: 64px 1fr 48px 120px;
  gap: 8px;
  padding: 8px 8px;
  border-bottom: 1px solid rgba(230, 215, 255, 0.08);
}

.bbs__rowItem:hover {
  background: rgba(182, 108, 255, 0.07);
}

.bbs__colNo,
.bbs__colR,
.bbs__colUpd {
  font-family: var(--mchan-font-mono);
  font-size: 12px;
  color: rgba(244, 238, 255, 0.70);
}

.bbs__colSubj {
  min-width: 0;
}

.bbs__subject {
  display: block;
  color: rgba(244, 238, 255, 0.92);
  font-weight: 650;
}

.bbs__snippet {
  display: block;
  margin-top: 4px;
  color: rgba(244, 238, 255, 0.62);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.bbs__empty {
  padding: 12px 8px;
  color: rgba(244, 238, 255, 0.60);
  border-bottom: 1px solid rgba(230, 215, 255, 0.08);
}

.bbs__op {
  border: 1px solid rgba(230, 215, 255, 0.10);
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.16);
  padding: 10px;
}

.bbs__opHead {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.bbs__meta {
  font-family: var(--mchan-font-mono);
  font-size: 11px;
  color: rgba(244, 238, 255, 0.62);
}

.bbs__opBody {
  margin-top: 10px;
  white-space: pre-wrap;
  line-height: 1.55;
  color: rgba(244, 238, 255, 0.84);
  overflow-wrap: anywhere;
}

.bbs__replies {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.bbs__reply {
  border: 1px solid rgba(230, 215, 255, 0.08);
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.12);
  padding: 10px;
}

.bbs__replyHead {
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  font-family: var(--mchan-font-mono);
  font-size: 11px;
  color: rgba(244, 238, 255, 0.62);
  align-items: baseline;
}

.bbs__replyNo {
  color: rgba(182, 108, 255, 0.90);
}

.bbs__replyMeta {
  margin-left: auto;
}

.bbs__replyBtn {
  border: 1px solid rgba(230, 215, 255, 0.10);
  border-radius: 6px;
  background: rgba(0, 0, 0, 0.16);
  color: rgba(244, 238, 255, 0.72);
  font-family: var(--mchan-font-mono);
  font-size: 11px;
  padding: 4px 8px;
  cursor: pointer;
}

.bbs__replyBtn:hover {
  background: rgba(182, 108, 255, 0.08);
  border-color: rgba(182, 108, 255, 0.25);
}

.bbs__replyBody {
  margin-top: 8px;
  white-space: pre-wrap;
  line-height: 1.55;
  color: rgba(244, 238, 255, 0.86);
  overflow-wrap: anywhere;
}

.bbs__composer {
  margin-top: 12px;
  border: 1px solid rgba(230, 215, 255, 0.10);
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.16);
  padding: 10px;
}

.bbs__composerTitle {
  font-family: var(--mchan-font-display);
  font-size: 14px;
  margin-bottom: 10px;
}

.bbs__field {
  margin-bottom: 10px;
}

.bbs__label {
  font-family: var(--mchan-font-mono);
  font-size: 11px;
  color: rgba(244, 238, 255, 0.65);
  margin-bottom: 6px;
}

.bbs__input,
.bbs__textarea {
  width: 100%;
  border: 1px solid rgba(230, 215, 255, 0.14);
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.16);
  color: rgba(244, 238, 255, 0.90);
  padding: 8px;
  font-family: var(--mchan-font-ui);
  font-size: 13px;
}

.bbs__textarea {
  resize: vertical;
}

.bbs__row {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
}

.bbs__btn {
  border: 1px solid rgba(230, 215, 255, 0.12);
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.16);
  color: rgba(244, 238, 255, 0.85);
  font-family: var(--mchan-font-mono);
  font-size: 12px;
  padding: 8px 10px;
  cursor: pointer;
}

.bbs__btn--primary {
  border-color: rgba(182, 108, 255, 0.45);
  background: rgba(182, 108, 255, 0.10);
}

.bbs__btn--danger {
  border-color: rgba(255, 91, 200, 0.40);
  background: rgba(255, 91, 200, 0.08);
}

.bbs__hint {
  margin-top: 8px;
  font-size: 12px;
  color: rgba(244, 238, 255, 0.60);
}

.bbs__footer {
  margin-top: 10px;
  padding-top: 8px;
  border-top: 1px solid rgba(230, 215, 255, 0.10);
  font-size: 11px;
  color: rgba(244, 238, 255, 0.55);
}

@media (max-width: 520px) {
  .bbs__listHead,
  .bbs__rowItem {
    grid-template-columns: 56px 1fr 40px 0px;
  }

  .bbs__colUpd {
    display: none;
  }
}
